.js-body {
    overflow: hidden;
    margin-right: 17px;
    height: auto;

    .header__outer {
        height: 100vh;
        overflow: auto;

        .container {
            width: 100%;
        }
    }

    .burger-menu {
        margin-top: 0;
        transform: translateY(0);
        z-index: 9999;
        transition: z-index .01s .15s;
    }
}

.burger-menu {
    margin-top: -70px;
    position: relative;
    width: 100%;
    min-height: calc(100% - 70px);
    transform: translateY(-100%);
    font-size: 18px;
    line-height: 1.5;
    background-color: #fff;
    transition: all 0.5s ease-in-out;
    z-index: 9998;

    > .flexible {
        flex-direction: column;
        justify-content: space-between;
        flex-wrap: nowrap;
        min-height: 667px;
    }
}

.burger-menu__nav {
    margin-top: 2.03528vmax;
    width: 100%;
    text-align: center;

    li + li {
        margin-top: 5px;
    }

    a {
        display: inline-block;
        padding: 0 20px 3px;
        font-size: 30px;
        line-height: 2;
        color: #000;

        &:hover {
            padding-bottom: 0;
            border-bottom: 3px solid #000;
        }
    }
}

.burger-menu__bottom {
    margin-top: 25px;
    text-align: center;

    .footer {
        width: 100%;
    }
}

.lang {
    justify-content: center;
    margin-bottom: 30px;

    li {
        width: 45px;
        text-transform: uppercase;
        border: 2px solid #000;
        font-size: 16px;
        line-height: 1.9375;
        transition: all .2s ease-in-out;

        &:hover,
        &.active {
            background-color: #000;
            color: #fff;
        }

        &.active {
            cursor: default;
        }
    }

    li + li {
        margin-left: 10px;
    }

    a {
        display: block;
        color: inherit;
    }
}

