.main {
    position: relative;
    overflow: hidden;
}

.main__text-bg {
    position: absolute;
    top: 11vh;
    left: -42px;
    font-size: 300px;
    line-height: 1;
    opacity: 0.05;
    z-index: -1;
}

.promo {
    position: relative;
    text-align: center;
    overflow: hidden;

    .container {
        flex-direction: column;
        justify-content: center;
        height: 100vh;
        min-height: 480px;
        background: transparent url('../images/promo-bg-sm.svg') no-repeat right -31px top 15vh;
        background-size: auto 143px;
    }

    h1 {
        font-size: 30px;
        margin: 0 0 30px;
    }
}

.promo__tagline {
    max-width: 250px;
    margin: 0 auto;
    font-size: 24px;
}

.works {
    position: relative;
    margin-top: -100px;
    z-index: 10;
}

.works__item {
    width: 100%;
    margin-bottom: 14px;
}

.works__row {
    width: 100%;
}

.works__info {
    margin-top: 26px;
    font-size: 18px;
    line-height: 1.6667;
    text-align: center;

    p + p {
        margin-top: 25px;
    }
}

.btn {
    display: inline-block;
    font-size: 15px;
    line-height: 1.4;
    padding: 20px 62px 19px;
    margin-top: 40px;
    color: #fff;
    background-color: #40d3ad;
    letter-spacing: 0.1em;
    text-decoration: none;
    border-radius: 30px;
    transition: all .2s ease-in-out;

    &:hover {
        opacity: 0.85;
    }
}

.instagram {
    margin-top: 40px;
    letter-spacing: 0.1em;

    > .flexible {
        justify-content: space-between;
        margin-top: 40px;
    }
}

.instagram__title {
    text-align: center;
    text-transform: uppercase;

    span {
        color: $color--inst;
    }
}


.instagram__item {
    width: calc((100% - 14px) / 2);
    margin-bottom: 14px;

    &:nth-child(5) {
        display: none;
    }
}

.footer {
    font-size: 14px;
    margin-top: 36px;
    padding: 40px 0;
    border-top: 1px solid #e9e9ea;
    text-align: center;

    .copyrights {
        margin-top: 15px;
        font-size: 12px;
    }
}

.footer__contacts {
    line-height: 2;
}

.footer__contacts,
.footer__col {
    width: 100%;
}

.footer__col {
    margin-top: 30px;
}

.social-list {
    line-height: 1.71429;

    li {
        display: inline-block;
    }

    li + li {
        margin-left: 15px;
    }

    a {
        display: inline-block;
        transition: all .2s ease-in-out;
        border-bottom: 1px solid transparent;
    }

    .link--inst {
        color: $color--inst;

        &:hover {
            border-bottom-color: $color--inst;
        }
    }

    .link--fb {
        color: $color--fb;

        &:hover {
            border-bottom-color: $color--fb;
        }
    }
}

@import '640';
@import '1024';
@import '1280';
@import '1920';
